        })
        .then(() => {
          this.login_email = ''
          this.login_password = ''
          this.$router.push({
            name: 'index'
          })
        })
        .catch((err) => {
          if (err.code === 'auth/user-disabled') {
            this.loginErrorMsg =
							'このアカウントはロックされています。'
          } else {
            this.loginErrorMsg =
							'メールアドレスまたはパスワードが間違っています。'
          }
        })
    }
